@import url(https://fonts.googleapis.com/css?family=Major+Mono+Display);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.setting-area .ant-form label {
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  background: #f12711;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(right, #f12711, #f5af19);
  background: -webkit-linear-gradient(left, #f12711, #f5af19);
  background: linear-gradient(to right, #f12711, #f5af19);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.annotation-table {
  margin-top: 50px; }
  .annotation-table .text-gradient {
    margin-left: 10px;
    font-weight: bold;
    font-size: 32px;
    background: #7f7fd5;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(right, #7f7fd5, #86a8e7, #91eae4);
    background: -webkit-linear-gradient(left, #7f7fd5, #86a8e7, #91eae4);
    background: linear-gradient(to right, #7f7fd5, #86a8e7, #91eae4);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .annotation-table Button {
    margin-top: 20px; }

.App {
  background-color: #fafafa;
  padding: 0px 50px; }
  .App .app-title {
    font-size: 9vw;
    text-align: center;
    color: #ee53a8;
    font-family: 'Major Mono Display', monospace; }
  .App .githubBtn {
    color: #8c8c8c;
    border-color: #8c8c8c;
    position: absolute;
    top: 10px;
    right: 16px; }
    .App .githubBtn a {
      color: #8c8c8c;
      text-decoration: none; }
  .App .result-title {
    margin-top: 132px;
    font-weight: bold;
    text-align: center;
    background: #12c2e9;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(right, #12c2e9, #c471ed, #f64f59);
    background: -webkit-linear-gradient(left, #12c2e9, #c471ed, #f64f59);
    background: linear-gradient(to right, #12c2e9, #c471ed, #f64f59);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

